.sendForm {
    display: flex;
    margin-top: 56px;
    margin-bottom: 32px;
}
.textarea {
    width: 976px;
    height: auto;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    margin-right: 44px;
    padding: 20px 24px;
    border: none;

}

.button {
    width: 120px;
    height: 40px;
    background: #0066CC;
    border-radius: 20px;
    border: none;
    color: #FFFFFF;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
}

.button:hover {
    background: #00cc22;
}
.button:active {
    background: #cc0000;
}
